import React from "react";
import ReactDOM from "react-dom";
import { ThemeProvider } from "styled-components";
import { Normalize } from "styled-normalize";
import { theme } from "./theme/theme";
import GlobalStyles from "./theme/global";
import List from "./components/List.js";
import Search from "./components/Search.js";
import Badge from "./components/Badge.js";
import websites from "./websites";

import Navigation, { VerticalList, HorizontalList } from "react-key-navigation";

class ReactTVApp extends React.Component {
  constructor() {
    super();

    this.state = {
      active: null,
      lists: websites[0]["content"].map(function (x) {
        var titulo = x["title_content"];
        return titulo;
      }),
      danamicInsert: false
    };
  }

  changeFocusTo(index) {
    this.setState({ active: index });
  }

  onBlurLists() {
    this.setState({ active: null });
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        danamicInsert: true
      });
    }, 1000);
  }

  render() {
    let search;
    if (websites[0]["search"] !== "") {
      search = <Search />;
    }
    const { lists } = this.state;
    return (
      <React.StrictMode>
        <ThemeProvider theme={theme}>
          <Normalize />
          <GlobalStyles />
          <Navigation>
            <div id="container">
              <HorizontalList>
                <div class="mainbox">
                  <VerticalList navDefault>
                    {search}
                    {this.state.danamicInsert}
                    <VerticalList
                      id="content"
                      onBlur={() => this.onBlurLists()}
                    >
                      <Badge fixed>Clepnid</Badge>
                      {lists.map((list, i) => (
                        <List
                          indice={i}
                          title={list}
                          onFocus={() => this.changeFocusTo(i)}
                          visible={
                            this.state.active !== null
                              ? i >= this.state.active
                              : true
                          }
                        />
                      ))}
                    </VerticalList>
                  </VerticalList>
                </div>
              </HorizontalList>
            </div>
          </Navigation>
        </ThemeProvider>
      </React.StrictMode>
    );
  }
}
const rootElement = document.getElementById("root");
ReactDOM.render(<ReactTVApp />, rootElement);
