import React from "react";

import { Focusable } from "react-key-navigation";

export default class Search extends React.Component {
  constructor() {
    super();

    this.state = {
      active: false
    };
  }

  onBlur() {
    this.setState({ active: false });
    document.getElementById("js-search").blur();
  }

  onFocus() {
    this.setState({ active: true });
    document.getElementById("js-search").focus();
  }

  onClick() {
    this.setState({ active: true });
    document.getElementById("js-search").focus();
  }

  render() {
    return (
      <Focusable
        onFocus={() => this.onFocus()}
        onBlur={() => this.onBlur()}
        navDefault
      >
        <div
          class={
            this.state.active ? "container-search-focus" : "container-search"
          }
          id={this.state.active ? "buscar-en-foco" : ""}
        >
          <input
            onClick={() => this.onClick()}
            id="js-search"
            placeholder="Search..."
            type="text"
          />

          <i
            onClick={() => this.onClick()}
            id="buscador"
            class="fa fa-search"
          ></i>
        </div>
      </Focusable>
    );
  }
}
