import React from "react";
import { Focusable, HorizontalList } from "react-key-navigation";
import styled from "styled-components";
import websites from "../websites";

const cardWidth = 320;
const borderRadius = 8;
const transition = "all 0.45s ease";

const Screenshot = styled.figure`
  z-index: 200;
  position: relative;
  margin: 0;
  padding: 0;
  width: ${cardWidth}px;
  height: 200px;
  background: url(${(props) => props.image}) 0 0 no-repeat;
  background-size: cover;
  border-radius: ${borderRadius}px ${borderRadius}px 0 0;
  overflow: hidden;
  backface-visibility: hidden;
  transition: ${transition};

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0);
    transition: ${transition};
  }
`;

const Content = styled.div`
  z-index: 200;
  position: relative;
  padding: 20px 20px 30px;
`;

const Title = styled.span`
  display: block;
  margin-bottom: 4px;
  font-size: 1.25em;
  font-weight: 500;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: ${transition};
`;

const Description = styled.span`
  display: block;
  font-size: 0.875em;
  color: #999999;
  transition: ${transition};
  transition-delay: 0.04s;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;

const BottomBar = styled.span`
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 10px;
  background: ${(props) => props.background && props.background};
  border-radius: 0 0 ${borderRadius}px ${borderRadius}px;
  transition: ${transition};
`;

const Style = styled.button`
  position: relative;
  flex-shrink: 0;
  width: ${cardWidth}px;
  text-align: left;
  background: #ffffff;
  border-radius: ${borderRadius}px;
  cursor: pointer;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.12), 0 20px 20px -10px rgba(0, 0, 0, 0.125);
  transition: ${transition};

  
  &:hover {
    transform: scale(1.04);

    ${Title},
    ${Description},
    ${BottomBar} {
      transform: scale(0.92);
    }

    ${Title} {
      transform: translateY(-10px);
    }

    ${Description} {
      transform: translateY(-12px);
    }

    ${BottomBar} {
      border-radius: ${borderRadius - 2}px;
      transform: translateY(-14px) scale(0.9);
    }

    ${Screenshot} {
      transform: translateY(4px) scale(0.92);
      border-radius: ${borderRadius - 2}px;

      &::before {
        background: rgba(0, 0, 0, 0.1);
      }
    }
  }
  
`;

class ToogleItem extends React.Component {
  constructor({ hexa, title, description, image, goTo }) {
    super();

    this.state = {
      active: false
    };
    this.hexadecimal = hexa;
    this.titulo = title;
    this.descripcion = description;
    this.imagen = image;
    this.goTo = goTo;
  }

  irA() {
    window.location.assign(this.goTo);
  }
  render() {
    return (
      <Focusable
        onFocus={() => this.setState({ active: true })}
        onBlur={() => this.setState({ active: false })}
        onClick={() => this.irA()}
      >
        <div id={this.state.active ? "item-focus" : ""} class="item ">
          <Style id="caja">
            <p
              id={this.state.active ? "p-focus" : ""}
              style={{ display: "none" }}
            >
              {this.goTo}
            </p>
            <Screenshot id="imagen" image={this.imagen} />
            <Content id="contenido">
              <Title id="titulo">{this.titulo}</Title>
              <Description id="descripcion">{this.descripcion}</Description>
              <BottomBar id="botonBar" background={this.hexadecimal} />
            </Content>
          </Style>
        </div>
      </Focusable>
    );
  }
}

export default class List extends React.Component {
  constructor({ indice }) {
    super();
    this._lastFocus = null;
    this.lists = websites[0]["content"].map(function (x) {
      var webs = x["webs"];
      return webs;
    });
    this.indice = indice;
  }

  componentDidMount() {
    const width =
      Math.floor(this.content.scrollWidth * this.content.clientWidth) + 20;
    if (this.content.getElementsByClassName("hz-list")[0]) {
      this.content.getElementsByClassName("hz-list")[0].style.width =
        width * 5 + "px";
    }
  }

  onFocus(index) {
    if (this._lastFocus === index) {
      return;
    }

    if (this.props.onFocus) {
      this.props.onFocus();
    }

    if (this.content) {
      const items = this.content.getElementsByClassName("item");
      const offsetWidth = items[0].offsetWidth + 20;
      this.content.scrollLeft = offsetWidth * index;
      const conent = document.querySelector("#hz-list-focus");
      conent.scrollLeft = offsetWidth * index;
    }

    this._lastFocus = index;
  }

  render() {
    let h1;
    if (this.props.title !== "") {
      h1 = <h1>{this.props.title}</h1>;
    }
    return (
      <div class={"contentgroup " + (this.props.visible ? "" : "fading-out")}>
        {h1}
        <div
          class="content"
          ref={(content) => {
            this.content = content;
          }}
        >
          <HorizontalList
            class={"hz-list "}
            id={this.props.visible ? "hz-list-focus" : ""}
            style={{ overflow: "auto", display: "block" }}
            onFocus={(index) => this.onFocus(index)}
            onBlur={() => {
              this._lastFocus = null;
            }}
          >
            <div class="hola">
              {this.lists[this.indice].map((website, index) => (
                <ToogleItem
                  key={index}
                  hexa={website.hexa}
                  title={website.title}
                  goTo={website.goTo}
                  description={website.description}
                  image={website.image}
                />
              ))}
            </div>
          </HorizontalList>
        </div>
      </div>
    );
  }
}
